<template>
  <div>
    <div class="header_settlement">
      <van-nav-bar title="订单结算" left-arrow @click-left="onClickLeft">
        <van-icon name="ellipsis" slot="right" />
      </van-nav-bar>
    </div>
    <div class="settlement_address" @click="toaddress">
      <div class="txt">
        <span class="txt_name">{{this.$store.state.edit.list[this.$store.state.edit.index].name}}</span>
        <span class="txt_phone">{{this.$store.state.edit.list[this.$store.state.edit.index].tel}}</span>
      </div>
      <div class="address_main">
        <van-icon name="location-o" />
        <span
          class="txt_address"
        >{{this.$store.state.edit.list[this.$store.state.edit.index].address}}</span>
      </div>
      <van-icon name="arrow" class="settlement_icon" />
    </div>
    <div class="settlement_title">
      <span class="icon">当当网</span>
    </div>

    <!--  -->
    <div v-for="(item,index) in newarr" :key="index">
      <van-card
        :title="item.title"
        :desc="item.desc"
        :num="item.num"
        :price="formatPrice(item.price)"
        :thumb="item.thumb"
      ></van-card>
    </div>
    <!--  -->

    <van-cell title="包装升级" is-link value="请选择" />
    <van-cell title="支付方式" is-link value="网上支付" />
    <van-cell title="支付方式" is-link value="网上支付" />

    <van-submit-bar :price="sum" button-text="提交订单" @submit="onSubmit" />
  </div>
</template>

<style lang="scss">
.header_settlement {
  .van-icon {
    color: black;
    font: 20px/1 "vant-icon";
  }
  .van-nav-bar .van-icon {
    color: black;
  }
}
.settlement_address {
  width: 100%;
  box-sizing: border-box;
  padding: 15px 20px;
  position: relative;
  background-color: #fff;
  font-size: 14px;
  .txt {
    .txt_name {
      display: block;
      float: left;
    }
    .txt_phone {
      display: block;
      float: left;
      margin-left: 20px;
    }
  }
  .txt::after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    clear: both;
  }
  .txt_address {
    display: block;
    width: 70%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .settlement_icon {
    position: absolute;
    right: 10px;
    margin-top: -8px;
    top: 50%;
    // top: 50%;
    // margin-top: 8px;
    z-index: 9999;
  }
  .address_main {
    position: relative;
    .van-icon-location-o {
      position: absolute;
      margin-left: -15px;
    }
  }
}
</style>

<script>
export default {
  data() {
    return {
      newarr: []
    };
  },
  methods: {
    formatPrice(price) {
      return (price / 100).toFixed(2);
    },
    onClickLeft() {
      this.$router.push("/gwc");
    },
    onSubmit() {
      for (var k = 0; k < this.newarr.length; k++) {
        this.$store.state.integral.time.push(new Date());
        let nnm = Math.floor(Math.floor(Math.random() * 1000000000));
        this.$store.state.integral.number.push(nnm);
       this.$store.state.Cart.infonum= this.$store.state.Cart.infonum-this.newarr[k].num;
      }

      this.$router.push("/submit");
      this.$store.state.Cart.dingdan = this.$store.state.Cart.dingdan.concat(
        this.newarr
      );
      if (this.$store.state.Cart.goodslists.length == this.newarr.length) {
        this.$store.state.Cart.goodslists = [];
      } else {
        for (var i = 0; i < this.$store.state.Cart.checkedGoods.length; i++) {
          if (this.$store.state.Cart.checkedGoods[i] >= 0) {
            for (var j = 0; j < this.$store.state.Cart.goodslists.length; j++) {
              if (
                this.$store.state.Cart.checkedGoods[i] ==
                this.$store.state.Cart.goodslists[j].id
              ) {
                this.$store.state.Cart.goodslists.splice(j, 1);
              }
            }
          }
        }
      }
      this.$store.state.Cart.checkedGoods = [];
      // window.console.log(this.$router.state.Cart.goodslists)
    },
    toaddress() {
      this.$store.state.edit.isflag = true;
      this.$router.push("/address");
    }
  },
  computed: {
    sum() {
      let a = 0;
      // for (var i = 0; i < this.$store.state.Cart.checkedGoods.length; i++) {
      //   if (this.$store.state.Cart.checkedGoods[i]) {
      //     a +=
      //       this.$store.state.Cart.goodslists[i].price *
      //       this.$store.state.Cart.goodslists[i].num;
      //   }
      // }
      //
      for (var i = 0; i < this.$store.state.Cart.checkedGoods.length; i++) {
        if (this.$store.state.Cart.checkedGoods[i] >= 0) {
          for (var j = 0; j < this.$store.state.Cart.goodslists.length; j++) {
            if (
              this.$store.state.Cart.checkedGoods[i] ==
              this.$store.state.Cart.goodslists[j].id
            ) {
              a +=
                this.$store.state.Cart.goodslists[j].price *
                this.$store.state.Cart.goodslists[j].num;
            }
          }
        }
      }
      return a;
    }
  },
  created() {
    window.console.log(this.$store.state.Cart.checkedGoods);
    window.console.log(this.$store.state.Cart.goodslists);
    for (var i = 0; i < this.$store.state.Cart.checkedGoods.length; i++) {
      if (this.$store.state.Cart.checkedGoods[i] >= 0) {
        for (var j = 0; j < this.$store.state.Cart.goodslists.length; j++) {
          if (
            this.$store.state.Cart.checkedGoods[i] ==
            this.$store.state.Cart.goodslists[j].id
          ) {
            this.newarr.push(this.$store.state.Cart.goodslists[j]);
          }
        }
      }
    }
    window.console.log(this.newarr);
  }
};
</script>